"use client";
import { useEffect, useState } from "react";
import { useTicker } from "@/context/TickerContext";
import { fetchTopPerformers } from "@/lib/api";

export default function Sidebar() {
    const [topStocks, setTopStocks] = useState([]);
    const { setSelectedTicker } = useTicker();

    useEffect(() => {
        async function loadData() {
            try {
                const topStocksData = await fetchTopPerformers()
                setTopStocks(topStocksData.slice(0, 10));
            } catch (error) {
                console.error("Error fetching data", error);
            }
        }
        loadData();
    }, []);


    return (
        <aside className="w-42 h-auto bg-darkgray p-4 pt-20 flex flex-col">
            {topStocks.map((stock, index) => (
                <button
                    key={stock.Ticker}
                    onClick={() => setSelectedTicker(stock.Ticker)}
                    className="text-gray-300 hover:bg-gray-700 p-2 rounded transition"
                >
                    {stock.Ticker}
                </button>
            ))}
        </aside>
    );
}
